body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(17, 25, 54);
  color: rgb(215, 220, 236);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5, h6 {
  color: rgb(215, 220, 236);
  font-weight: 700;
}

h1 {
  font-size: 2.125rem;
}

h2 {
  font-size: 1.25rem;
}

main {
  min-height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
}

.flex-table {
  background: rgb(33, 41, 70);
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(17, 25, 54);
  border-radius: 8px;
  overflow: hidden;
}

.table-head {
  background: rgb(33, 41, 70);
  position: sticky;
  font-weight: bold;
  font-size: 0.875rem;
  display: flex;
  flex-direction: row;
  padding: 6px 0;
}

.table-cell:first-child, .table-cell:first-child {
  width: 24px;
  flex-grow: 0;
  flex-shrink: 0;
}

.table-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: rgb(189, 200, 240);
  border-top: 1px solid rgba(189, 200, 240, 0.082);
}

.table-cell {
  width: calc(25% - 24px/4);
  box-sizing: border-box;
  padding: 6px 16px;
  flex-grow: 0;
  flex-shrink: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

input {
  background: rgba(189, 200, 240, 0.082);
  border: none;
  color: rgb(189, 200, 240);
  padding: 8px 16px;
  border-radius: 4px;
}

input:focus {
  outline: 1px solid rgb(17, 25, 54);
}